  }
}
.logo__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}
.logo__title-link {
  margin: 5px 9px 0 0;
}
.logo__wrapper a:hover {
  opacity: 0.8;
}
.logo__wrapper a:active {
  opacity: 0.6;
}
.logo__toggle--close {
  margin: 0;
  padding: 0;
  border: none;
  width: 24px;
  height: 24px;
  background-image: url("../img/logo_mobile--close.svg");
  background-color: #fff;
  background-repeat: no-repeat;
  cursor: pointer;
}
.logo__toggle--open {
  margin: 0;
  padding: 0;
  border: none;
  width: 24px;
  height: 18px;
  background-image: url("../img/button-logo_mobile.svg");
  background-color: #fff;
  background-repeat: no-repeat;
  cursor: pointer;
}
.logo__toggle--nojs {
  background-image: none;
  cursor: default;
}
@media (min-width: 768px) {
  .logo__title-link {
    display: none;
  }
  .logo__toggle {
    display: none;
  }
  .logo__img {
    display: none;
  }
  .logo__image-link {
    width: 174px;
    height: 52px;
    background-image: url("../img/logo-tablet.svg");
    background-color: #fff;
    background-repeat: no-repeat;
  }
}
@media (min-width: 1220px) {
  .logo__image-link {
    width: 202px;
    height: 59px;
    background-image: url("../img/logo-desktop.svg");
    background-color: #fff;
    background-repeat: no-repeat;
  }
}
.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}
.main-nav__link {
  display: block;
  padding: 22px 0 24px 0;
  text-decoration: none;
  text-transform: uppercase;
  color: #000;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  cursor: pointer;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__item::before {
  position: absolute;
  content: "";
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
}
.main-nav__item:nth-child(3n)::after {
  position: absolute;
  content: "";
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
}
.main-nav--close .main-nav__list {
  display: none;
}
.main-nav--open .main-nav__list {
  display: block;
}
.main-nav--nojs .main-nav__list {
  display: block;
}
@media (min-width: 768px) {
  .main-nav__item--current {
    border-bottom: 2px solid #68B738;
    padding-bottom: 10px;
  }
  .main-nav__link {
    padding: 0;
    text-decoration: none;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .main-nav__item::before,
  .main-nav__item:nth-child(3n)::after {
    display: none;
  }
  .main-nav__wrapper .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    width: 440px;
    justify-content: space-between;
  }
}
@media (min-width: 1220px) {
  .main-nav__wrapper .main-nav__list {
    width: 479px;
    margin-bottom: 5px;
  }
  .main-nav__link {
    padding: 0;
    text-decoration: none;
    color: #fff;
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
  }
  .main-nav__item--current {
    border-bottom: 2px solid #fff;
    padding-bottom: 0;
  }
}
@media (min-width: 768px) {
