    padding-bottom: 10px;
  }
}
@media (min-width: 1220px) {
  .main-nav__catalog-link {
    color: #111111;
  }
  .main-nav__item--current-catalog {
    border-bottom: 2px solid #68B738;
  }
}
.background-cat {
  display: flex;
  position: relative;
  justify-content: center;
}
.background-cat__container::before {
  content: "";
  position: absolute;
  height: 302px;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #68B738;
  opacity: 0.85;
  z-index: -1;
}
.background-cat__container::after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  height: 302px;
  width: 100%;
  background-image: url("../img/product/background-cat-mobile.png");
  background-repeat: no-repeat;
  background-position-x: 50%;
  z-index: -2;
  background-color: #295724;
}
