  .background-cat__container {
    position: relative;
  }
  .background-cat__container::after {
    background-image: none;
    background-color: #fff;
  }
  .background-cat__container::before {
    background-color: #fff;
  }
}
@media (min-width: 1220px) {
  .background-cat__container {
    position: absolute;
    width: 50%;
    right: 0;
  }
  .background-cat__container::after {
    background-image: url("../img/product/background-cat-desktop.png");
    height: 694px;
    top: -126px;
    background-color: #295724;
    background-size: cover;
    background-position-x: 0%;
  }
  .background-cat__container::before {
    top: -126px;
    height: 694px;
    background-color: #68B738;
  }
}
@media (min-width: 1220px) and (min-resolution: 2dppx) {
  .background-cat__container::after {
    background-image: url("../img/product/background-cat-desktop@2x.png");
    background-size: cover;
